  margin-right: auto;
  width: 88.75vw;
}
.products-fifth-section-text,
.products-fifth-section-text p,
.products-fifth-section-text li {
  font-size: 16px;
  font-weight: 400;
}
.products-fifth-section-left-col {
  display: none;
}
.products-last-section {
  min-height: inherit;
  margin-bottom: 5.82vw;
  width: 88.28vw;
  margin: auto;
}
.products-last-section,
.products-last-section p,
.products-last-section li {
  font-size: 16px;
  font-weight: 400;
}
.products-last-section-title {
  font-size: 7.66vw;
  font-weight: 300;
  font-family: "Lato";
  text-align: center;
  margin-bottom: 0px;
  margin-top: 37px;
  letter-spacing: 2.7px;
}
.products-last-section-col-title {
  font-size: 24px;
  font-weight: 500;
  margin: 0 0px 19px 0px;
  line-height: 1em;
}
.products-last-section-tab-logo {
  width: 81.25vw;
  display: block;
  margin: auto;
  margin-top: 40px;
  margin-bottom: 32px;
}
.products-last-section-right-col {
  background-color: #e9e9e9;
  width: calc(67% - 7vw);
  margin-right: 7vw;
  margin-left: 5vw;
  border-radius: 5px;
  display: inline-block;
  padding: 10px 18px;
  position: relative;
  vertical-align: top;
  float: left;
  z-index: 2;
  padding-left: 38px;
}
.products-last-section-right-col::before {
  background-color: #e9e9e9;
  content: "\00a0";
  display: block;
  height: 34px;
  position: absolute;
  top: 36px;
  transform: rotate(29deg) skew(-35deg);
  width: 30px;
  left: -11px;
  z-index: 1;
}

/* Search results mobile styles */
.search-results-header {
  width: 100%;
  height: 127px;
}
.search-results-header-title {
  font-size: 18px;
  letter-spacing: 0.5px;
  margin-bottom: 4px;
}
.search-results-header-subtitle {
  font-size: 13.2px;
  letter-spacing: 0.4px;
}
.search-results-content {
  padding: 31.5px 5vw 31.5px 5vw;
}
.search-results-content .pagination .page-numbers {
  font-size: 13px;
  line-height: 0.53;
  padding: 0 20px;
}
.search-results-form-wrapper {
  margin-bottom: 53.5px;
}
.search-results-elem {
  margin-bottom: 17px;
}
.search-results-elem-title {
  font-size: 25px;
  letter-spacing: 1.2px;
  margin-bottom: 0px;
}
.search-results-elem-description {
  letter-spacing: 0.8px;
  font-size: 16px;
  line-height: 21px;
}
.search-results-elem-description:after {
  margin-top: 0px;
}
.search-results-elem-description > :last-child {
  margin-bottom: 13px;
}
.search-results-elem-link,
.search-results-elem-description {
  font-size: 16px;
  font-weight: normal;
}
.search-results-elem-title {
  line-height: 0.8;
  margin-bottom: 3px;
}
}
