/* General Common styles */
.search-form {
  position: relative;
  display: flex;
  flex-direction: row;
  width: 100%;
  height: 43px;
  border: solid 1px #dfdfdf;
  background-color: #ffffff;
  padding-right: 1px;
}
.search-form-input {
  display: flex;
  flex-grow: 1;
  height: 100%;
  border: 0px;
  outline: 0px;
  box-sizing: border-box;
  padding: 0px 0px 3px 16.5px;
  min-width: 0px;
  font-family: "Lato";
  font-size: 16px;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: 8.5px;
  letter-spacing: 1.1px;
  color: #2e2e2e;
  background-color: #ffffff;
}
.search-form-submit {
  transform: scaleX(-1);
  width: 55px;
  height: 100%;
  background-color: #ffffff;
  padding: 8px 15px 8px 15px;
  outline: 0px;
  border: 0px;
  box-sizing: border-box;
  background-image: url('/wp-content/themes/sixgill/images/search_icon.svg');
  background-size: 30px 30px;
  background-position: 14px 4px;
  background-repeat: no-repeat;
}
.search-form-placeholder {
  visibility: hidden;
  display: inline-block;
  position: absolute;
  top: 0px;
  bottom: 0px;
  left: 0px;
  right: 0px;
  pointer-events: none;
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: "Lato";
  font-size: 15px;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  letter-spacing: 1.5px;
  text-align: center;
  color: #999999;
  background-color: #f8f8f8;
}
.search-form-placeholder-icon {
  width: 20px;
  height: 20px;
  margin-right: 9px;
  transform: scaleX(-1);
}
.search-form:not(.always-active) .search-form-input:not(:focus):not(:valid) {
  opacity: 0;
}
.search-form:not(.always-active) .search-form-input:not(:focus):not(:valid) + .search-form-submit {
  visibility: hidden;
}
.search-form:not(.always-active) .search-form-input:not(:focus):not(:valid) ~ .search-form-placeholder {
  visibility: visible;
}
